import React from 'react';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import classNames from 'classnames';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

import ErrorOutlineIcon from '@material-ui/icons/ErrorOutline';
import CheckCircleOutlinedIcon from '@material-ui/icons/CheckCircleOutlined';
import Tooltip from '@material-ui/core/Tooltip';

import Utilities from './Utilities';
import { useCookies } from 'react-cookie';
import queryString from 'query-string';

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    display: 'flex',
    flexDirection: 'row',
    background: '#2d2d2f',
    transition: 'background-color .2s ease-in-out',
    cursor: 'pointer'
  },
  icon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    width: theme.spacing(7),
    backgroundColor: '#4d4d4d',
    fontWeight: 'bold',
    transition: 'background-color .2s ease-in-out'
  },
  iconHovered: {
    backgroundColor: '#666666'
  },
  hovered: {
    backgroundColor: '#3e3e41'
  },
  title: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'flex-end'
  },
  version: {
    paddingLeft: theme.spacing(1)
  },
  officialState: {
    marginLeft: 'auto'
  },
  listRoot: {
    backgroundColor: theme.palette.background.paper,
    marginLeft: '-16px',
    marginRight: '-16px'
  },
  listitem: {
    display: 'flex',
    justifyContent: 'flex-end'
  },
  official: {
    color: '#64dd17'
  },
  unofficial: {
    color: 'yellow'
  },
  banned: {
    color: '#ff3d00',
    cursor: 'pointer'
  },
  officialIcon: {
    marginLeft: 'auto',
    marginRight: '16px',
    alignSelf: 'center'
  },
  link: {
    color: '#2196f3'
  },
  cardRoot: {
    maxWidth: 'unset' /* ,
    width: '400px',
    height: '104px' */
  }
}));
function OfficialTooltip({ children }) {
  return (
    <Tooltip interactive arrow title='This is an official plugin.'>
      {children}
    </Tooltip>
  );
}
function UnofficialTooltip({ children }) {
  return (
    <Tooltip interactive arrow title="This is an unofficial plugin. It's either taking a while, or is yet to be verified.">
      {children}
    </Tooltip>
  );
}

function BannedTooltip({ children }) {
  return (
    <Tooltip interactive arrow title='Click me for more info.'>
      {children}
    </Tooltip>
  );
}

const FLAG_BANNED = 1 << 0;

function TooltipHandler({ children, officialState }) {
  switch (officialState) {
    case -1:
      return <BannedTooltip>{children}</BannedTooltip>;
    case 0:
      return <UnofficialTooltip>{children}</UnofficialTooltip>;
    case 1:
      return <OfficialTooltip>{children}</OfficialTooltip>;
    default:
      return children;
  }
}

export function PluginCard({ card, forceOpen, onDownlaod }) {
  const classes = useStyles();
  const [cookies, setCookie] = useCookies(['PluginCard']);
  const [hovered, setHovered] = React.useState(false);
  const [infoOpen, setInfo] = React.useState(forceOpen || false);
  const [warningOpen, setWarning] = React.useState((!cookies.banWarned && forceOpen) || false);
  const CL1 = {};
  const CL2 = {};
  CL1[classes.iconHovered] = hovered;
  CL2[classes.hovered] = hovered;
  const isBanned = card.official === -1;
  const officialClassName = card.official === 1 ? classes.official : isBanned ? classes.banned : classes.unofficial;
  const handleInfoClose = () => {
    setInfo(false);
    window.history.replaceState({}, '', window.location.origin);
  };
  const handleCardClicked = () => {
    setInfo(true);
    if (isBanned && !cookies.banWarned && !(global.Flags & FLAG_BANNED)) {
      global.Flags |= FLAG_BANNED;
      setWarning(true);
      setCookie('banWarned', true, {
        maxAge: 604800
      });
    }
    window.history.replaceState({}, '', `?${queryString.stringify({ plugin: card.name })}`);
  };
  const handleWarningClose = e => {
    setWarning(false);
    if (!cookies.banWarned && !(global.Flags & FLAG_BANNED)) {
      global.Flags |= FLAG_BANNED;
      setCookie('banWarned', true, {
        maxAge: 604800
      });
    }
  };
  const onOfficialStateClicked = e => {
    if (!isBanned) return;
    e.preventDefault();
    e.stopPropagation();
    setWarning(true);
    if (!cookies.banWarned) setCookie('banWarned', true, { maxAge: 604800 });
    if (!(global.Flags & FLAG_BANNED)) global.Flags |= FLAG_BANNED;
  };
  const IconComponent = card.official === 0 || isBanned ? ErrorOutlineIcon : CheckCircleOutlinedIcon;
  return (
    <React.Fragment>
      <Grid className={classes.cardRoot} item={true} key={card.name} xs={3} sm={4}>
        <Card className={classNames(classes.root, CL2)} onMouseEnter={() => setHovered(true)} onMouseLeave={() => setHovered(false)} onClick={handleCardClicked}>
          <div className={classNames(classes.icon, CL1)}>{card.id}</div>
          <CardHeader title={card.name} subheader={card.version} />
          <TooltipHandler officialState={card.official}>
            <IconComponent onClick={onOfficialStateClicked} className={classNames(classes.officialIcon, officialClassName)}></IconComponent>
          </TooltipHandler>
        </Card>
      </Grid>
      <Dialog disableScrollLock open={infoOpen} onClose={handleInfoClose}>
        <DialogTitle className={classes.title} disableTypography>
          <Typography variant='h5'>{card.name}</Typography>
          <Typography className={classes.version}>v{card.version}</Typography>
          <div className={classes.officialState}>
            <TooltipHandler officialState={card.official}>
              <Typography onClick={onOfficialStateClicked} className={officialClassName}>
                {card.official === 1 ? 'Official' : card.official === -1 ? 'Banned' : 'Unofficial'}
              </Typography>
            </TooltipHandler>
          </div>
        </DialogTitle>
        <DialogContent>
          <DialogContentText>{card.description}</DialogContentText>
          <List className={classes.listRoot} dense>
            <ListItem button divider>
              <ListItemText primary='State' />
              <ListItemText className={classes.listitem} primary={card.state} primaryTypographyProps={{ align: 'right' }} />
            </ListItem>
            <ListItem button divider>
              <ListItemText primary='Authors' />
              <ListItemText className={classes.listitem} primary={card.authors} primaryTypographyProps={{ align: 'right' }} />
            </ListItem>
            <ListItem button divider>
              <ListItemText primary='Updated' />
              <ListItemText className={classes.listitem} primary={card.updated} primaryTypographyProps={{ align: 'right' }} />
            </ListItem>
            <ListItem button>
              <ListItemText primary='Released' />
              <ListItemText className={classes.listitem} primary={card.released} primaryTypographyProps={{ align: 'right' }} />
            </ListItem>
          </List>
        </DialogContent>
        <DialogActions>
          <Button href={card.src} target='_blank'>
            Source
          </Button>
          {card.ghdl && (
            <Button href={`https://betterdiscord.net/ghdl?id=${card.ghdl}`} target='_blank'>
              Alt Download
            </Button>
          )}
          <Button onClick={() => (onDownlaod(true), Utilities.saveFile(card.raw, () => onDownlaod(false)))}>Download</Button>
        </DialogActions>
      </Dialog>
      <Dialog disableScrollLock open={warningOpen} onClose={() => {}}>
        <DialogTitle>Read closely.</DialogTitle>
        <DialogContent>
          <Typography>
            Plugins marked as Banned mean they go against the BetterDiscord server rules. <br />
            As such, <b>do not ask for support of these plugins anywhere, except my own Discord server.</b>
            <br />
            If you refuse to read this warning and you get banned from the BetterDiscord and/or BetterDiscord2 server because of it, be aware <b>it's not my problem and you brought this onto yourself.</b>
            <br />
            <br />
            And to clarify, these plugins will <b>not</b> get your Discord account banned, just banned from the <b>BetterDiscord servers</b> if you announce you have them.
            <br />
            If you require support for these plugins, <b>join my own Discord server instead</b>, where people are more friendly overall (they are forced ;)).
            <br />
            <Link className={classes.link} href='https://discord.gg/NYvWdN5' target='_blank' color='inherit'>
              https://discord.gg/NYvWdN5
            </Link>
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleWarningClose}>I have read the above warning</Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
}
