const CssTextField = withStyles({
  root: {
    '& label.Mui-focused': {
      color: '#2196f3'
    },
    '& .MuiOutlinedInput-root': {
      '&:hover fieldset': {
        borderColor: 'light gray'
      },
      '&.Mui-focused fieldset': {
        borderColor: '#2196f3'
      }
    }
  }
})(TextField);

const CARD_WIDTH = 416;
const CARD_HEIGHT = 120;

/**
 * @author DiscordApp
 */
function RoughlyMatches(e, t) {
  var n = t.length,
    r = e.length;
  if (r > n) return !1;
  if (r === n) return e === t;
  e: for (var i = 0, o = 0; i < r; i++) {
    for (var a = e.charCodeAt(i); o < n; ) if (t.charCodeAt(o++) === a) continue e;
    return !1;
  }
  return !0;
}

function SlideTransition(props) {
  return <Slide {...props} direction='up' />;
}

const parsedURL = queryString.parse(window.location.search);
let plugin = parsedURL.plugin && cardData.data.find(m => m.name === parsedURL.plugin);

function Root() {
  const classes = useStyles();
  /* cards container width */
  const [bind, { width }] = useMeasure();
  const [snackbarOpen, setSnackbar] = React.useState(false);
  const [speedDialVisible, setSpeedDialVisibility] = React.useState(false);
  const [speedDial, setSpeedDial] = React.useState(false);
  React.useEffect(() => void setTimeout(() => setSpeedDialVisibility(true), 1000));
  const [cookies, setCookie, removeCookie] = useCookies(['DisplaySettings']);
  const [filter, setFilter] = React.useState({
    search: parsedURL.q || '',
    banned: cookies.bannedPlugins || false
  });
  const [debouncedSetFilter] = useDebouncedCallback(value => {
    setFilter(value);
    if (value.banned) {
      setCookie('bannedPlugins', true, {
        maxAge: 3.154e7
      });
    } else {
      removeCookie('bannedPlugins');
    }
  }, 100);
  if (plugin && plugin.official === -1 && !cookies.bannedPlugins) {
    debouncedSetFilter({ ...filter, banned: true });
  }
  const columns = Math.floor(width / CARD_WIDTH);

  const sorted = cardData.data
    .filter(item => {
      if (filter.search.length) {
        const normalized = item.name.toLowerCase();
        if (normalized.indexOf(filter.search) === -1 && !RoughlyMatches(filter.search, normalized)) return;
      }
      if (!filter.banned && item.official === -1) return false;
      return true;
    })
    .sort((a, b) => (a.id < b.id ? -1 : a.id > b.id ? 1 : 0));
  /* are all rows full? */
  const full = sorted.length % columns === 0;
  /* if not, get first item that is on last row */
  const nearLast = !full ? sorted.length - (sorted.length % columns) : null;
  /* calculate how many columns the last row will have */
  const lastRowColumns = Math.max(sorted.length - nearLast, 0);
  const gridItems = sorted.map((item, index) => {
    /* if item is on last row and last row is not full, grab the column count for the last row to center the
    items */
    const wantedColumns = nearLast !== null && index >= nearLast ? lastRowColumns : columns;
    /* even and odd offset calculation is different */
    const isEven = wantedColumns % 2 === 0;
    let x;
    /* get column for current item */
    const column = index % wantedColumns;
    if (isEven) {
      /* how many items on the left or right
      eg if there's 4 items
      2 on the left, 2 on the right of the center of the cards container
       */
      const sideCount = wantedColumns / 2;
      /* we need to check if the item is past the middle point */
      if (column >= sideCount) {
        /* if it is, check if it's the first item past the middle point, we don't want it too far from the
        middle */
        const mid = column - (sideCount - 1) === 1;
        /* if we use full card width, there will be enough space between left and right to fit another card */
        const cardWidth = mid ? CARD_WIDTH / 2 : CARD_WIDTH;
        /* tldr
        get item count from the middle and multiply it by cardWidth, and remove half card width if it's not
        the first item past the middle
        otherwise all but the first card past the middle will overshoot and be too far to the right
         */
        x = cardWidth * (column - (sideCount - 1)) - (mid ? 0 : CARD_WIDTH / 2);
      } else {
        /* if it's pre middle point, check if it's the first item right before the middle */
        const mid = column - (sideCount - 1) === 0;
        /* same as before */
        const cardWidth = mid ? CARD_WIDTH / 2 : CARD_WIDTH;
        /* get item count from the middle, but to the left this time and multiply it by cardWidth
        since we're going into negative, add half card width if it's not right before the middle, like before
         */
        x = -(cardWidth * (sideCount - column)) + (mid ? 0 : CARD_WIDTH / 2);
      }
    } else {
      /* odd offset calculation */
      /* get the center item index */
      const center = Math.floor(wantedColumns / 2);
      /* if we're past the center */
      if (column > center) {
        /* get the item count past the center multiplied by the full card width */
        x = CARD_WIDTH * (column - center);
      } else {
        /* otherwise do the same, but into negative */
        x = -(CARD_WIDTH * (center - column));
      }
    }
    /* get row number and multiply it by card height */
    const y = CARD_HEIGHT * Math.floor(index / columns);
    return {
      ...item,
      /* do extra checks, first render has container width of NaN which breaks everything */
      xy: [isNaN(x) ? 0 : x, isNaN(y) || Infinity === y ? 0 : y]
    };
  });

  const transitions = useTransition(gridItems, item => item.name, {
    from: ({ xy }) => ({ xy, height: 0, opacity: 0 }),
    enter: ({ xy }) => ({ xy, height: CARD_HEIGHT, opacity: 1 }),
    update: ({ xy }) => ({ xy }),
    leave: { height: 0, opacity: 0 },
    config: { mass: 3, tension: 500, friction: 70 }
  });

  const cards = transitions.map(({ item, props: { xy, ...rest }, key }) => {
    return (
      <a.div className={classes.cardWrapper} key={key} style={{ transform: xy.interpolate((x, y) => `translate3d(${x}px,${y}px,0)`), ...rest }}>
        <PluginCard onDownlaod={setSnackbar} forceOpen={plugin && plugin.name === item.name} card={item} />
      </a.div>
    );
  });
  const containerHeight = Math.ceil(sorted.length / columns) * CARD_HEIGHT;
  const handleOpenLink = link => {
    window.open(link, '_blank');
    setSpeedDial(false);
  };
  plugin = null;
  return (
    <div className={classes.mainContent}>
      <AppBar position='relative'>
        <Toolbar>
          <BDLogo className={classes.icon} />
          <Typography variant='h6' color='inherit' noWrap>
            Lighty's BandagedBetterDiscord stuff
          </Typography>
          <form className={classes.searchInput} noValidate autoComplete='off'>
            <CssTextField defaultValue={filter.search} variant='outlined' size='small' onChange={e => debouncedSetFilter({ ...filter, search: e.target.value.toLowerCase() })} label='Search' />
          </form>
        </Toolbar>
      </AppBar>
      <main>
        <Container className={classes.cardGrid}>
          <Grid {...bind} container spacing={4} justify='center' style={{ height: isNaN(containerHeight) || Infinity === containerHeight ? 0 : containerHeight }}>
            {/* sorted.map(item => (
              <PluginCard card={item}></PluginCard>
            )) */ cards}
          </Grid>
        </Container>
      </main>
      <footer className={classes.footer}>
        <Copyright />
      </footer>
      <SpeedDial ariaLabel='Speed dial' className={classes.speedDial} hidden={!speedDialVisible} icon={<SpeedDialIcon />} onClose={() => setSpeedDial(false)} onOpen={() => setSpeedDial(true)} open={speedDial}>
        <SpeedDialAction key='paypal' icon={<AttachMoney />} tooltipTitle='Paypal' tooltipOpen onClick={() => handleOpenLink('https://paypal.me/lighty13')} />
        <SpeedDialAction classes={{ staticTooltipLabel: classes.speedDialTooltip }} key='kofi' icon={<AttachMoney />} tooltipTitle='Ko-fi' tooltipOpen onClick={() => handleOpenLink('https://ko-fi.com/lighty_')} />
        <SpeedDialAction classes={{ staticTooltipLabel: classes.speedDialTooltip }} key='bannedplugins' icon={<VisibilityOff />} tooltipTitle='Toggle banned plugins' tooltipOpen onClick={() => debouncedSetFilter({ ...filter, banned: !filter.banned })} />
        <SpeedDialAction classes={{ staticTooltipLabel: classes.speedDialTooltip }} key='discord' icon={<Help />} tooltipTitle='Discord Server' tooltipOpen onClick={() => handleOpenLink('https://discord.gg/NYvWdN5')} />
      </SpeedDial>
      <Snackbar open={snackbarOpen} TransitionComponent={SlideTransition} message={<span>Downlading...</span>} />
    </div>
  );
}

export default function App() {
  return (
    <MuiThemeProvider theme={theme}>
      <CssBaseline />
      <Root />
    </MuiThemeProvider>
  );
}
